import React, { Component } from 'react'

export default class TodoStyle extends Component {
    constructor(props) {
        super(props);
        this.state = {
            element: '',
            items: [],
            error: ''

        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.deleteTask = this.deleteTask.bind(this);
    }



    onChange = (e) => {
         this.setState({ [e.target.name]: e.target.value, })



        localStorage.setItem('task', JSON.stringify(e.target.value));

    }

    onSubmit = (e) => {
        console.log(e)
        e.preventDefault()
        
        this.state.element !== '' ?
            this.setState({
                element: '',
                items: [...this.state.items, { element: this.state.element }],
                error:''
            }) : this.setState({ error: 'Empty task is like doing nothing...right ?' });

        localStorage.setItem('items', JSON.stringify(this.state.items))

    }


    deleteTask = (index) => {
        const arr = this.state.items;
        arr.splice(index, 1);
        localStorage.setItem("items", JSON.stringify(arr));
        this.setState({
            items: arr
        })

    }



    componentDidMount() {
        const taskSaved = JSON.parse(localStorage.getItem("task"));
        const itemsSaved = JSON.parse(localStorage.getItem('items'));
        taskSaved !== null ? this.setState({
            element: taskSaved,
            items: itemsSaved,
        }) : this.setState({
            element: '',
            items: []
        });

    }

    displayList = () => {

        return this.state.items.map((item, index) => {
            return (

                <div className="task-card" key={index}>

                    <div className="task-card-body">
                        <h4>
                            {item.element}

                        </h4>
                        <i
                            className="fas fa-times"

                            onClick={() => this.deleteTask(index)}
                        ></i>
                    </div>
                </div>
            )
        })
    }

    render() {
        return (
            <React.Fragment>


                <div className="tasks-card">
                    <div className="header">Sunny (Todo) List</div>
                    <div className="error"><h4>{this.state.error ? this.state.error : ''}</h4></div>
                    <div className="tasks-form">
                        <form onSubmit={this.onSubmit}>


                            <input
                                type="text"
                                className="tasks-input"
                                name="element"
                                placeholder="TASK"
                                onChange={this.onChange}
                                value={this.state.element}
                                autoFocus
                            />

                            <button className="tasks-btn">Add Task</button>
                        </form>
                    </div>
                    <div className="task-list">
                        <span>
                            {/* <i class="fab fa-stack-overflow"></i> */}
                            <h3>Your Tasks List</h3>  </span>
                        {this.displayList()}
                    </div>
                </div>



            </React.Fragment>
        )
    }
}
